@import url('https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap');

body {
  color: #fff;
  font-family: 'Roboto Condensed', sans-serif;
  background-image: url(https://images.unsplash.com/photo-1526256262350-7da7584cf5eb?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1050&q=80);

  background-position: top top;
  background-size: cover;
  margin: 0;
}

.App {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  background: rgb(145, 35, 27);
  background: linear-gradient(
    0deg,
    rgba(145, 35, 27, 1) 0%,
    rgba(145, 35, 27, 1) 25%,
    rgba(145, 35, 27, 0.6) 50%
  );
}

.container {
  width: 100%;
  margin: 0% auto;
}
.col-4 {
  width: 25%;
  float: left;
}

h1 {
  font-size: 38px;
  line-height: 1;
}

@media (min-width: 768px) {
  .container {
    width: 1100px;
  }
  h1 {
    font-size: 58px;
  }
}

.title-text {
  font-size: 7rem;
  color: rgba(235, 212, 13, 0.884);
  margin-bottom: -2rem;
}

.blink_me {
  animation: blinker 2s linear infinite;
  color: rgb(245, 180, 39);
}

@keyframes blinker {
  50% {
    opacity: 0;
    color: antiquewhite;
  }
}

.form-link {
  color: rgb(245, 180, 39);
}

.registration-link {
  display: block;
  width: fit-content;
  margin: auto;
  margin-bottom: 1rem;
  text-decoration: none;
  color: #fff;
  font-weight: bold;
  background-color: #538fbe;
  padding: 0.5rem 1.5rem;
  font-size: 1.5rem;
  border: 1px solid #2d6898;
  background-image: linear-gradient(
    bottom,
    rgb(73, 132, 180) 0%,
    rgb(97, 155, 203) 100%
  );
  background-image: -o-linear-gradient(
    bottom,
    rgb(73, 132, 180) 0%,
    rgb(97, 155, 203) 100%
  );
  background-image: -moz-linear-gradient(
    bottom,
    rgb(73, 132, 180) 0%,
    rgb(97, 155, 203) 100%
  );
  background-image: -webkit-linear-gradient(
    bottom,
    rgb(73, 132, 180) 0%,
    rgb(97, 155, 203) 100%
  );
  background-image: -ms-linear-gradient(
    bottom,
    rgb(73, 132, 180) 0%,
    rgb(97, 155, 203) 100%
  );
  background-image: -webkit-gradient(
    linear,
    left bottom,
    left top,
    color-stop(0, rgb(73, 132, 180)),
    color-stop(1, rgb(97, 155, 203))
  );
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  text-shadow: 0px -1px 0px rgb(0 0 0 / 50%);
  -webkit-box-shadow: 0px 6px 0px #2b638f, 0px 3px 15px rgb(0 0 0 / 40%),
    inset 0px 1px 0px rgb(255 255 255 / 30%),
    inset 0px 0px 3px rgb(255 255 255 / 50%);
  -moz-box-shadow: 0px 6px 0px #2b638f, 0px 3px 15px rgba(0, 0, 0, 0.4),
    inset 0px 1px 0px rgba(255, 255, 255, 0.3),
    inset 0px 0px 3px rgba(255, 255, 255, 0.5);
  box-shadow: 0px 6px 0px #2b638f, 0px 3px 15px rgb(0 0 0 / 40%),
    inset 0px 1px 0px rgb(255 255 255 / 30%),
    inset 0px 0px 3px rgb(255 255 255 / 50%);
  -webkit-transition: all 0.1s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  transform: rotateX(20deg);
  -webkit-transform: rotateX(20deg);
  cursor: pointer;
  transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
  -webkit-perspective: 400;
}

.registration-text {
  font-size: 1.25rem;
  color: #e9c3c1;
}
